import { evaluate } from '../node_modules/mathjs/';
// 
const displayResult = document.getElementById("display-result");
const displayProcess = document.getElementById("display-process");
// NodeList
const buttons = document.querySelectorAll("button");
// 
let currentInput = "";
let result = "";
let fullExpression = "";
// 
buttons.forEach(button => {
    button.addEventListener("click", () => {
        const value = button.value;
        // 
        if (!isNaN(Number(value)) || value === ".") {
            handleNumber(value);
        }
        // 
        else if (value === "+" || value === "-" || value === "*" || value === "/") {
            handleOperator(value);
        }
        // 
        else if (value === "%") {
            handlePercentage();
        }
        // 
        else if (value === "(" || value === ")") {
            handleParentheses(value);
        }
        // =
        else if (value === "=") {
            calculateResult();
        }
        // C
        else if (value === "C") {
            clearDisplay();
        }
    });
});
// 
function handleNumber(value) {
    if (currentInput.length < 10) { // 10
        currentInput += value;
        fullExpression += value;
        displayProcess.value = fullExpression; // 
    }
}
// 
function handleOperator(value) {
    if (currentInput !== "") {
        currentInput = "";
        fullExpression += value; // 
        displayProcess.value = fullExpression; // 
    }
}
// 
function handlePercentage() {
    if (currentInput !== "") {
        currentInput = (parseFloat(currentInput) / 100).toString();
        fullExpression = fullExpression.slice(0, -currentInput.length) + currentInput; // 
        displayResult.value = fullExpression;
    }
}
// 
function handleParentheses(value) {
    fullExpression += value;
    displayProcess.value = fullExpression; // 
}
// 
function calculateResult() {
    try {
        const result = evaluate(fullExpression);
        displayProcess.value += "=";
        displayResult.value = result.toString(); // 
        fullExpression = result.toString(); // 
        currentInput = ""; // 
    }
    catch (error) {
        displayResult.value = "Error"; // 
    }
}
// 
function clearDisplay() {
    currentInput = "";
    fullExpression = "";
    displayResult.value = "";
    displayProcess.value = "";
}
